<script>
import {
  Cell,
  CellGroup,
  Card,
  Toast,
  Radio,
  RadioGroup,
  SubmitBar,
  PasswordInput,
  Dialog,
  NumberKeyboard,
  Overlay,
  Icon,
} from "vant";

export default {
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Card.name]: Card,
    [Radio.name]: Radio,
    [RadioGroup.name]: RadioGroup,
    [SubmitBar.name]: SubmitBar,
    [PasswordInput.name]: PasswordInput,
    [Dialog.Component.name]: Dialog.Component,
    [NumberKeyboard.name]: NumberKeyboard,
    [Overlay.name]: Overlay,
    [Icon.name]: Icon,
  },

  data() {
    return {
      radio: '1',
      goodsId: 1,
      show: false,
      payPassword: '',
      showKeyboard: false,
      goods: {
        title: "Sakose悠然舒畅按摩精油肩颈刮痧油精油",
        price: 68,
        express: "免运费",
        desc: '规格：180g',
        remain: 19,
        collected: false,
        num: 1,
        thumb: [
          "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
          "https://img.yzcdn.cn/public_files/2017/10/24/1791ba14088f9c2be8c610d0a6cc0f93.jpeg"
        ],
        picture: 'https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg',
      },
    };
  },

  methods: {
    formatPrice() {
      return "¥" + (this.goods.price / 100).toFixed(2);
    },

    onSubmit() {
      this.show = true;
    },

    onSubmitPayPassword() {
      this.show = false;
    },

    onCancel() {
      this.show = false;
    },

    onInput(key) {
      this.payPassword = (this.payPassword + key).slice(0, 6);
      if (this.payPassword.length !== 6) {
        return;
      }
      if (this.payPassword === '123456') {
        this.$router.replace('order/pay-result');
      } else {
        Toast('密码错误');
      }
    },

    onDelete() {
      this.payPassword = this.payPassword.slice(0, this.payPassword.length - 1);
    }
  }
};
</script>

